type CardStatesProviderProps = {
	children: React.ReactNode
}

export const CardStatesProvider = ({ children }: CardStatesProviderProps) => {
	const [eventProperties, setEventProperties] = useState({
		day: -1,
		group: 0,
		openedCard: { groupNum: -1, cardNum: -1 },
		checkedCards: [
			new Array(events[0].lectures.length).fill(-1),
			new Array(events[1].lectures.length).fill(-1),
			new Array(events[2].lectures.length).fill(-1),
		],
	})

	const setDay = (selectedDay: number) => {
		setEventProperties({
			...eventProperties,
			day: eventProperties.day === selectedDay ? -1 : selectedDay,
			group: 0,
			openedCard: { groupNum: -1, cardNum: -1 },
		})
	}

	const setOpenedCard = (grNum: number, cNum: number) => {
		setEventProperties({
			...eventProperties,
			openedCard: { groupNum: grNum, cardNum: cNum },
		})
	}

	const setCheckedCard = (grNum: number, cNum: number) => {
		const arr = eventProperties.checkedCards.slice()
		if (eventProperties.day === 1) {
			if (cNum === 1) {
				if (arr[1][0] !== -1 && arr[1][2] !== -1 && arr[1][3] !== -1) {
					return
				}
			} else if (cNum === 0) {
				if (arr[1][1] !== -1 && arr[1][2] !== -1 && arr[1][3] !== -1) {
					return
				}
			} else if (cNum === 2) {
				if (arr[1][0] !== -1 && arr[1][1] !== -1 && arr[1][3] !== -1) {
					return
				}
			} else if (cNum === 3) {
				if (arr[1][0] !== -1 && arr[1][1] !== -1 && arr[1][2] !== -1) {
					return
				}
			}
		} else if (eventProperties.day === 2) {
			if (cNum === 1) {
				if (arr[2][0] !== -1 && arr[2][2] !== -1) {
					return
				}
			} else if (cNum === 0) {
				if (arr[2][1] !== -1 && arr[2][2] !== -1) {
					return
				}
			} else if (cNum === 2) {
				if (arr[2][0] !== -1 && arr[2][1] !== -1) {
					return
				}
			}

			if (cNum === 3) {
				arr[2][4] = -1
			} else if (cNum === 4) {
				arr[2][3] = -1
			}
		}
		arr[eventProperties.day][cNum] =
			arr[eventProperties.day][cNum] === grNum ? -1 : grNum
		setEventProperties({ ...eventProperties, checkedCards: arr })
	}

	return (
		<CardStatesContext.Provider
			value={[
				eventProperties,
				setDay,
				events,
				setOpenedCard,
				setCheckedCard,
			]}
		>
			{children}
		</CardStatesContext.Provider>
	)
}
